const container = document.getElementById('root'); //idÍ∞íÏù¥ rootÎ•º Î∂àÎü¨Ïò¥
const ajax = new XMLHttpRequest();
const content = document.createElement('div');
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';
// Í≥µÏú†ÎêòÎäî Í∞íÏùÑ Í∞ùÏ≤¥Î°ú Î¨∂Ïñ¥Ï§å
const store = {
  currentPage: 1,
  maxPage: 0,
};

//ÎÑ§Ìä∏ÏõåÌÅ¨Î•º ÌÜµÌï¥ Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏ÏôÄ Í∞ùÏ≤¥Î°ú Î≥ÄÍ≤Ω
function getData(url) {
  ajax.open('GET', url, false); // Îç∞Ïù¥ÌÑ∞Î•º ÎèôÍ∏∞Ï†ÅÏúºÎ°ú Í∞ÄÏ†∏Ïò¥
  ajax.send(); //Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò¥
  store.maxPage = JSON.parse(ajax.response).length / 10;

  return JSON.parse(ajax.response); // jsonÌòïÏãùÏúºÎ°ú Î≥ÄÌôò
}

// Í∏ÄÎ™©Î°ùÏùÑ Í∞ÄÏ†∏Ïò§Îäî Ìï®Ïàò
function newsFeed() {
  const newsFeed = getData(NEWS_URL);
  const ul = document.createElement('ul'); //ÌÖåÍ∑∏Î•º ÏÉùÏÑ±

  const newsList = [];

  let template = `
  <div class="container mx-auto p-4">
    <h1>Hacker News</h1>
    <ul>
      {{__news_feed__}}
    </ul>
    <div>
      <a href="#/page/{{__prev_page__}}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
      <a href="#/page/{{__next_page__}}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
    </div>
  </div>
  `;

  for (let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i++) {
    newsList.push(`
    <li>
      <a href="#/show/${newsFeed[i].id}">
        ${newsFeed[i].title}(${newsFeed[i].comments_count})
      </a>
    </li>
  `);
  }

  template = template.replace('{{__news_feed__}}', newsList.join('')); // ÎßàÌÅ¨ÏóÖÏùÑ ÎåÄÏ≤¥
  template = template.replace(
    '{{__prev_page__}}',
    store.currentPage > 1 ? store.currentPage - 1 : 1
  );
  template = template.replace(
    '{{__next_page__}}',
    store.currentPage < store.maxPage ? store.currentPage + 1 : store.maxPage
  );

  container.innerHTML = template;
}

function newsDetail() {
  const id = location.hash.substring(7); // Ï£ºÏÜåÏôÄ Í¥ÄÎ†®Îêú Îç∞Ïù¥ÌÑ∞Î•º Ï†ÑÎã¨ #ÏùÑ Ï†úÍ±∞Ìïú Í∞íÏùÑ Ï∂úÎ†•
  const newsContent = getData(CONTENT_URL.replace('@id', id));

  container.innerHTML = `
  <h1> ${newsContent.title}</h1>
    <div>
      <a href="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
    </div>
  `;
}

// ÌôîÎ©¥Ï†ÑÌôò location.hashÏóêÏÑú #ÏùÄ ÎπàÍ∞íÏùÑ Î∞òÌôòÌï®
function router() {
  const routePath = location.hash;
  if (routePath === '') {
    newsFeed();
  } else if (routePath.indexOf('#/page/') >= 0) {
    store.currentPage = Number(routePath.substring(7));
    //indexOf ÏûÖÎ†• Î¨∏ÏûêÏó¥ÏùÑ Ï∞æÏïÑ ÏóÜÏúºÎ©¥ -1 Í∞í ÏïÑÎãàÎ©¥ ÏúÑÏπò Í∞íÏùÑ Ï†ÑÎã¨
    newsFeed();
  } else {
    newsDetail();
  }
}

// #ÏùÄ Ìï¥Ïãú Ìï¥ÏãúÍ∞Ä Î∞îÎÄåÏóàÏùÑÎïå Ïù¥Î≤§Ìä∏Í∞Ä Î∞úÏÉù hashchange Ïù¥Î≤§Ìä∏Î•º ÏÇ¨Ïö©(windowÏóêÏÑú Î∞úÏÉù)

window.addEventListener('hashchange', router);
router();
